.default-background {
    background-color: blue;
  }
  
  .clicked-background {
    background-color: red; 
    border-radius: 25em;
  }


.cards {
    display: inline-block;
    transition: transform .2s;
    position: relative;
    cursor:pointer;
    min-width:12rem;
    height: 18rem;
    z-index:0;
}

